<template>
	<transition name="fade" appear>
		<router-view></router-view>
	</transition>
</template>

<script>
	export default {
		name: 'App',
		computed: {
			Options () {
				const { Options } = this.$FeathersVuex.api;
				return Options;
			},
			user () {
				return this.$store.getters["auth/user"] || {};
			}
		},
		methods: {
			ghettoThemeSet (theme) {
				const root = document.getElementsByTagName( 'html' )[0];
				root.classList.add(theme === 'dark' ? 'theme-dark' : 'theme-light')
				root.classList.remove(theme === 'dark' ? 'theme-light' : 'theme-dark');
			},
			handleLogin (changes, area) {
				if (area === 'sync' && typeof changes.apiAccessToken !== "undefined") {
					if (changes.apiAccessToken.newValue) {
						this.$store.dispatch('auth/authenticate', {
							strategy: 'jwt',
							accessToken: changes.apiAccessToken.newValue
						}).then(() => {
							this.$router.push('/');
						})
						.catch(err => {
							console.log(err);
						});
					} else {
						this.$store.dispatch('auth/logout')
						.then(() => {
							this.$router.push('/sign-in');
						});
					}
				}
			}
		},
		mounted () {
			this.Options.find({
				query: {
					userId: this.user.id
				}
			}).then(res => {
				let options;
				if (res.total === 0) {
					options = new this.Options();
				} else {
					options = new this.Options(res.data[0]);
				}
				this.ghettoThemeSet(options.theme);
			});
			this.Options.on('patched', options => this.ghettoThemeSet(options.theme));
			this.$chrome.storage.onChanged.addListener(this.handleLogin);
		},
		beforeDestroy () {
			this.Options.off('patched', options => this.ghettoThemeSet(options.theme));
			this.$chrome.storage.onChanged.removeListener(this.handleLogin);
		}
	}
</script>

<style lang="scss">
	@import '../node_modules/bootstrap/scss/bootstrap';
	@import '../node_modules/bootstrap-vue/src/index.scss';
	@import '../src/assets/css/source.scss';
	.fade-enter-active,
	.fade-leave-active {
		transition: opacity 0.5s;
	}

	.fade-enter,
	.fade-leave-to {
		opacity: 0;
	}
</style>
